function App() {
  const [mode, setmode] = useState('light')
  const [modeColor, setmodeColor] = useState('Dark');
  // const [color, setcolor] = useState('dark')
  const toggleMode = () => {
    if (mode === 'light') {
      setmode('dark')
      setmodeColor('Light')
      document.body.style.backgroundColor = "#212529";
      document.body.style.color = "white";
      document.title = "Textutils - Dark Mode";
      // setcolor('light')
    }
    else {
      setmode('light')
      setmodeColor('Dark')
      document.body.style.backgroundColor = "#f8f9fa";
      document.body.style.color = "black";
      document.title = "Textutils - Light Mode";
      // setcolor('dark')
    }
  }
  return (
    <>
      {/* <Navbar title = "Textutilus" aboutText="About Me"/> */}
      {/* 
      <Navbar toggleMode={toggleMode} title="Textutilus" mode={mode} Dark={modeColor} aboutText="About Us" />
      <About />
      <TextForm title="Enter the text to analyze" /> */}
      <Navbar toggleMode={toggleMode} title="Textutilus" mode={mode} Dark={modeColor} aboutText="About Us" />
      <TextForm title="Enter the text to analyze" /> 
      {/* <BrowserRouter>
        <Routes>
          <Route exact path="/" element={<TextForm title="Enter your text to analuze" />}></Route>
          <Route exact path="/about" element={<About />}></Route>
        </Routes>
      </BrowserRouter> */}
    </>
  );

}

export default App;
