import React, { useState } from 'react'

export default function TextForm(props) {
    const handleUpclick = () => {
        let newText = text.toUpperCase();
        setText(newText);
        setAlert(<div class="alert alert-success" role="alert">
            <strong>Suceess !</strong> Your Text has been uppercase.
        </div>)
        setTimeout(() => {
            setAlert('');
        }, 3000);
    }
    const handleDownclick = () => {
        let newText = text.toLowerCase();
        setText(newText);
        setAlert(<div class="alert alert-success" role="alert">
            <strong>Suceess !</strong> Your Text has been lowercase.
        </div>)
        setTimeout(() => {
            setAlert('');
        }, 3000);
    }
    const handleOnChange = (event) => {
        // console.log("On change")
        setText(event.target.value);
    }
    const clearText = () => {
        let newText = '';
        setText(newText);
        setAlert(<div class="alert alert-success" role="alert">
            <strong>Suceess !</strong> Your Text has been cleared.
        </div>);
        setTimeout(() => {
            setAlert('');
        }, 3000);
    }
    const changeLanguage = () => {
        let newText = document.getElementById("myBox");
        // newText.style.color = "blue";
        // setText(newText);
        if (newText.style.fontSize === "30px") {
            newText.style.fontSize = "20px";
            setSize('Change Font Size');
            setAlert(<div class="alert alert-success" role="alert">
                <strong>Suceess !</strong> Your Text has been resize.
            </div>)
            setTimeout(() => {
                setAlert('');
            }, 3000);
        }
        else {
            newText.style.fontSize = "30px";
            setSize("Revert Size");
            setAlert(<div class="alert alert-success" role="alert">
                <strong>Suceess !</strong> Font size has been changed.
            </div>)
            setTimeout(() => {
                setAlert('');
            }, 3000);
        }

        console.log("Change size");
    }
    const copyText = () => {
        let copyText = text;
        navigator.clipboard.writeText(copyText);
        setAlert(<div class="alert alert-success" role="alert">
            <strong>Suceess !</strong> Your Text has been copied.
        </div>)
        setTimeout(() => {
            setAlert('');
        }, 3000);
    }
    const [text, setText] = useState('');
    const [FontSize, setSize] = useState('Change Font size')
    // const [clear, setClear] = useState('Clear Text');
    const [alert, setAlert] = useState('');

    const [linecount, setlinecount] = useState(0);
    const countline= ()=>{
           let textvalue = text;
           let lines = text.split(/\r|\r\n|\n/);
           setlinecount(lines.length)
    }
    // const [color, setcolor] = useState("color: red");
    // text = "Enter your text 2" // Wrong way to change the state
    // setText("New Text");  //Correct way to change the state
    return (
        <>
            <div className="container">
                {alert}
            </div>
            <div>
                <div className="mb-3 container">
                    <h1>{props.title}</h1>
                    <textarea style= {{backgroundColor:props.mode ==='dark'?'grey':'light'}} className="form-control" id="myBox" rows="8" onChange={handleOnChange} value={text}></textarea>
                    <button className="my-3 mx-2 btn btn-primary" onClick={handleUpclick}>Convert to Uppercase</button>
                    <button className="my-3 mx-2 btn btn-primary" onClick={handleDownclick}>Convert to Lowercase</button>
                    <button className="my-3 mx-2 btn btn-primary" onClick={changeLanguage}>{FontSize}</button>
                    <button className="my-3 mx-2 btn btn-primary" onClick={clearText}>Clear Text</button>
                    <button className="my-3 mx-2 btn btn-primary" onClick={copyText}>Copy Clipboard</button>
                    <button className="my-3 mx-2 btn btn-primary" onClick={countline}>Total line</button>
                </div>
                <div className="container">
                    <h1>Your Text summary</h1>
                    <p>{text.split(' ').length} words and {text.length} character</p>
                    <p>Time required to read this paragraph : {text.split(' ').length * 0.008} Minutes</p>
                    <h3>Preview</h3>
                    <p>{text.length>0?text:"Enter your text to preview"}</p>
                    <p>Total lines in the paragraph: {linecount}</p>
                </div>
            </div>

        </>
    )
}
